var EventEmitter = require('events').EventEmitter
var inherits = require('inherits')

var css = require('dom-css')
var insertcss = require('insert-css')
var path = require('path')
var isstring = require('is-string')
var themes = require('./themes')
var uuid = require('node-uuid')

module.exports = Plate
inherits(Plate, EventEmitter)

function Plate (items, baseOpts) {
  var hasRoot = baseOpts.root !== undefined;
  if (!(this instanceof Plate)) return new Plate(items, baseOpts)
  var self = this

  opts = baseOpts || {}
  opts.width = opts.width || 300
  opts.theme = opts.theme || 'dark'
  opts.theme = isstring(opts.theme) ? themes[opts.theme] : opts.theme
  opts.root = opts.root || document.body
  opts.position = opts.position

  // Create box
  var box = document.createElement('div')
  this.box = box
  this.box.style.display = "none"
  var id = uuid.v4()
  box.className = 'control-panel'
  box.id = 'control-panel-' + id

  // Create title bar
  this.bar = document.createElement('div');
  opts.root.insertBefore(this.bar, opts.root.childNodes[0]);
  this.bar.style.zIndex = 10000;
  //opts.root.appendChild(this.bar);

  var barCss = {
    background: opts.theme.background1,
    width: '100%',
    height: '16px',
    paddingTop: '8px',
    paddingBottom: '8px',
    opacity: 1.0,
    position : "absolute",
    left : "0",
    top: "0"
  }

  // In some container
  if(hasRoot) {
      barCss["position"] = "relative"
      barCss["left"] = "0";
      if(opts.insertAboveRoot && hasRoot)
        barCss["top"] = "-32px";
      else
        barCss["top"] = "0";
  }
  else {
    console.log("No root");
      barCss["position"] = "absolute";
      barCss["left"] = "0";
      barCss["top"] = "0";
  }

  css(this.bar, barCss)

  var barText = this.bar.appendChild(document.createElement('div'))
  barText.innerHTML = opts.title

  css(barText, {
    width: '100%',
    "text-align": 'center',
    color: opts.theme.text2,
    //textTransform: 'uppercase',
    height: '20px',
    marginBottom: '4px',
    "font-family": "'Hack', monospace",
    "font-size": "11px",
    "-webkit-user-select": "none",
    "-moz-user-select": "none",
    "-ms-user-select": "none",
    "user-select": "none",
    "cursor": "default",
  })

  var barButton = this.bar.appendChild(document.createElement("button"));
  barButton.innerHTML = "Controls";
  css(barButton, {
    "font-family": "'Hack', monospace",
    "font-size": "11px",
    position: "absolute",
    top: "8px",
    //"background-color": opts.theme.text2,
    color: opts.theme.background1,
  });
  if (opts.position == 'top-right') 
    css(barButton, {
      right: "8px"
    });
  else
    css(barButton, {
      left: "8px"
    });
  barButton.onclick = function () {
    console.log(box)
    if(box.style.display != "none")
      box.style.display = "none"
    else
      box.style.display = "block"
  }

  this.bar.appendChild(box)

  var basecss = Buffer("","base64")
  var colorcss = Buffer("","base64")
  var rangecss = Buffer("","base64")
  var checkboxcss = Buffer("","base64")
  var buttoncss = Buffer("","base64")
  var intervalcss = Buffer("","base64")
  var selectcss = Buffer("","base64")

  rangecss = String(rangecss)
    .replace(new RegExp('{{ THUMB_COLOR }}', 'g'), opts.theme.foreground1)
    .replace(new RegExp('{{ TRACK_COLOR }}', 'g'), opts.theme.background2)
    .replace(new RegExp('{{ UUID }}', 'g'), id)
  checkboxcss = String(checkboxcss)
    .replace(new RegExp('{{ BOX_COLOR }}', 'g'), opts.theme.background2)
    .replace(new RegExp('{{ ICON_COLOR }}', 'g'), opts.theme.foreground1)
    .replace(new RegExp('{{ UUID }}', 'g'), id)
  buttoncss = String(buttoncss)
    .replace(new RegExp('{{ BUTTON_COLOR }}', 'g'), opts.theme.text2)
    .replace(new RegExp('{{ BUTTON_BG }}', 'g'), opts.theme.background2)
    .replace(new RegExp('{{ BUTTON_COLOR_HOVER }}', 'g'), opts.theme.text2)
    .replace(new RegExp('{{ BUTTON_BG_HOVER }}', 'g'), opts.theme.background2hover)
    .replace(new RegExp('{{ BUTTON_COLOR_ACTIVE }}', 'g'), opts.theme.background2)
    .replace(new RegExp('{{ BUTTON_BG_ACTIVE }}', 'g'), opts.theme.text2)
    .replace(new RegExp('{{ UUID }}', 'g'), id)
  intervalcss = String(intervalcss)
    .replace(new RegExp('{{ INTERVAL_COLOR }}', 'g'), opts.theme.foreground1)
    .replace(new RegExp('{{ TRACK_COLOR }}', 'g'), opts.theme.background2)
    .replace(new RegExp('{{ UUID }}', 'g'), id)
  selectcss = String(selectcss)
    .replace(new RegExp('{{ TEXT_COLOR }}', 'g'), opts.theme.text2)
    .replace(new RegExp('{{ BG_COLOR }}', 'g'), opts.theme.background2)
    .replace(new RegExp('{{ BG_COLOR_HOVER }}', 'g'), opts.theme.background2hover)
    .replace(new RegExp('{{ UUID }}', 'g'), id)
  insertcss(basecss)
  insertcss(rangecss)
  insertcss(colorcss)
  insertcss(checkboxcss)
  insertcss(buttoncss)
  insertcss(intervalcss)
  insertcss(selectcss)

  var elem = document.createElement('style')
  elem.setAttribute('type', 'text/css')
  elem.setAttribute('rel', 'stylesheet')
  elem.setAttribute('href', '//cdn.jsdelivr.net/font-hack/2.019/css/hack.min.css')
  document.getElementsByTagName('head')[0].appendChild(elem)

  css(box, {
    background: opts.theme.background1,
    width: opts.width,
    padding: '14px',
    paddingBottom: '8px',
    opacity: opts.opacity || 1.0
  })

  //if ((opts.position === 'top-right' || opts.position === 'top-left') || hasRoot)
    css(box, {position: 'absolute'})

  if (opts.position == 'top-right') 
    css(box, {"right": "0px"})
  else if(opts.position == 'top-left')
    css(box, {"left": "0px"})

  //if (opts.title) require('./components/title')(box, opts.title, opts.theme)

  var components = {
    button: require('./components/button'),
    text: require('./components/text'),
    range: require('./components/range'),
    checkbox: require('./components/checkbox'),
    color: require('./components/color'),
    interval: require('./components/interval'),
    select: require('./components/select')
  }

  var element
  var state = {}

  items.forEach(function (item) {
    if (item.type !== 'button') {
      state[item.label] = item.initial
    }
  })

  items.forEach(function (item) {
    element = components[item.type](box, item, opts.theme, id)

    element.on('initialized', function (data) {
      state[item.label] = data
    })

    element.on('input', function (data) {
      state[item.label] = data
      self.emit('input', state)
    })
  })


}
